import Axios from 'axios'

const state = {
  urlPref: 'https://lab.isaaclin.cn/nCoV/api',
  nationData: [],
  provinceCount: 34,
  shouldNationDataShown: false,
  chinaIndex: 0,
  chinaData: null
}

const mutations = {
  getInstantNationData(state, payload) {
    const self = this
    //console.log(payload)
    for (var i = 0; i < payload.length; i++) {
      if (payload[i].countryName == '中国') {
        if (payload[i].provinceShortName == '中国') {
          state.chinaIndex = i
          state.chinaData = payload[i]
          continue
        }
        state.nationData.push({
          provinceShortName: payload[i].provinceShortName,
          currentConfirmedCount: payload[i].currentConfirmefdCount,
          confirmedCount: payload[i].confirmedCount,
          suspectedCount: payload[i].suspectedCount,
          curedCount: payload[i].curedCount,
          deadCount: payload[i].deadCount
        })
      }
    }
    state.shouldNationDataShown = true
    console.log('finish')
    //console.log(state.nationData)
  },
  resetFlag(state) {
    if (state.shouldNationDataShown == true) {
      state.shouldNationDataShown = false
    }
  }
}

const actions = {
  getInstantNationData(context) {
    var param = '/area?latest=1'
    var index = 0
    Axios.get(state.urlPref + param)
      .then(function(res) {
        context.commit('getInstantNationData', res.data.results)
      })
      .catch(function(err) {
        console.log('error in getNationData. err message', err)
      })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
